"use client";

import { useEffect, useState } from "react";
import { getJSON, postJSON } from "@/lib/clientApi";
import type { InventoryItem } from "@/lib/models";

export function InventoryPanel({ ownerId }: { ownerId: string }) {
  const [items, setItems] = useState<InventoryItem[]>([]);
  const [feedback, setFeedback] = useState<string | null>(null);
  const [newItem, setNewItem] = useState({ id: "", name: "", quantity: 1 });
  const [removeItemId, setRemoveItemId] = useState("");
  const [removeQty, setRemoveQty] = useState(1);

  useEffect(() => {
    if (!ownerId) {
      setItems([]);
      return;
    }
    loadInventory(ownerId);
  }, [ownerId]);

  async function loadInventory(id: string) {
    try {
      const response = await getJSON<InventoryItem[]>(`/api/inventory/get?ownerId=${id}`);
      setItems(response);
      setFeedback(null);
    } catch (err) {
      setFeedback(getMessage(err));
    }
  }

  async function handleAdd(event: React.FormEvent<HTMLFormElement>) {
    event.preventDefault();
    if (!ownerId) {
      setFeedback("Informe um ownerId para manipular o inventário.");
      return;
    }
    const payload = {
      ownerId,
      item: {
        id: newItem.id || makeId(),
        name: newItem.name,
        quantity: Number(newItem.quantity)
      }
    };
    try {
      const response = await postJSON<InventoryItem[]>("/api/inventory/add", payload);
      setItems(response);
      setFeedback("Item adicionado.");
    } catch (err) {
      setFeedback(getMessage(err));
    }
  }

  async function handleRemove(event: React.FormEvent<HTMLFormElement>) {
    event.preventDefault();
    if (!ownerId) {
      setFeedback("Informe um ownerId para manipular o inventário.");
      return;
    }
    try {
      const response = await postJSON<InventoryItem[]>("/api/inventory/remove", {
        ownerId,
        itemId: removeItemId,
        quantity: Number(removeQty)
      });
      setItems(response);
      setFeedback("Item removido.");
    } catch (err) {
      setFeedback(getMessage(err));
    }
  }

  return (
    <div>
      <h3>Inventário</h3>
      {feedback && <p>{feedback}</p>}
      <ul className="list">
        {items.map((item) => (
          <li key={item.id}>
            <strong>{item.name}</strong> — {item.quantity}x ({item.id})
          </li>
        ))}
        {items.length === 0 && <li>Nenhum item registrado.</li>}
      </ul>

      <div className="grid">
        <form onSubmit={handleAdd}>
          <h4>Adicionar item</h4>
          <label htmlFor="item-name">Nome</label>
          <input
            id="item-name"
            required
            value={newItem.name}
            onChange={(event) => setNewItem((prev) => ({ ...prev, name: event.target.value }))}
          />
          <label htmlFor="item-quantity">Quantidade</label>
          <input
            id="item-quantity"
            type="number"
            min={1}
            value={newItem.quantity}
            onChange={(event) =>
              setNewItem((prev) => ({ ...prev, quantity: Number(event.target.value) }))
            }
          />
          <button className="button">Adicionar</button>
        </form>
        <form onSubmit={handleRemove}>
          <h4>Remover item</h4>
          <label htmlFor="remove-id">ID do item</label>
          <input
            id="remove-id"
            required
            value={removeItemId}
            onChange={(event) => setRemoveItemId(event.target.value)}
          />
          <label htmlFor="remove-qty">Quantidade</label>
          <input
            id="remove-qty"
            type="number"
            min={1}
            value={removeQty}
            onChange={(event) => setRemoveQty(Number(event.target.value))}
          />
          <button className="button">Remover</button>
        </form>
      </div>
    </div>
  );
}

function getMessage(err: unknown) {
  return err instanceof Error ? err.message : "Erro desconhecido";
}

function makeId() {
  if (typeof crypto !== "undefined" && typeof crypto.randomUUID === "function") {
    return crypto.randomUUID();
  }
  return `item-${Date.now()}`;
}
